import readingTime from 'reading-time'

export const postDateMixin = {
    computed: {
        timeToRead(){
          return readingTime(this.post.body).text
        },
        dayOfPost() {
          return this.post.created.substring(8, 10)
        },
        monthOfPost() {
            let d = new Date(this.post.created.substring(0,10))
            return d.toLocaleString('default', { month: 'short' })
        },
        yearOfPost() {
            return '\'' + this.post.created.substring(2,4)
        }
      },
}