<template>
  <div id="content-wrapper" class="container wrapper">
    <div class="single-blog col-md-8">
      <!-- Post Article
      =============================================-->
      <article class="post wow fadeIn clearfix">
        <div class="post-date">
          <span class="date-number">{{dayOfPost}}</span>
          <span class="date-month">{{monthOfPost}}</span>
          <span class="date-month">{{yearOfPost}}</span>
        </div>

        <div class="post-meta">
          <h2 class="post-title">{{post.title}}</h2>
          <div class="meta-post-top">
            <span class="comments">
              <i class="smaze icon-clock-1"></i>{{ timeToRead }}
            </span>
          </div>
        </div>

        <div class="post-content">
          <div class="post-thumb">
            <img :src="post.banner" alt="blog1" class="img-post" />
          </div>
          <div class="inner-post" v-html="post.body">
          </div>
        </div>

        <!-- meta bottom -->
        <div class="meta-bottom clearfix">
          <!-- social share -->
          <div class="social-share col-md-3">
            <h4 class="share-title">
              <i class="icon icon-share"></i>Share
            </h4>
            <a href="www.facebook.com" class="facebook">
              <i class="smaze icon-facebook"></i>
            </a>
            <a href="www.twitter.com" class="twitter">
              <i class="smaze icon-twitter-alt"></i>
            </a>
            <a href="www.linkedin.com" class="linkedin">
              <i class="smaze icon-linkedin"></i>
            </a>
          </div>
          <!-- social share end -->

          <!-- tags -->
          <div class="tags-bottom col-md-9">
            <h4 class="tag-title">
              <i class="smaze icon-tag-outline"></i>Tags
            </h4>
            <span>
              <router-link v-for="tag in post.tags" :key="tag.slug" :to="{name: 'tag-posts', params: {tag: tag.slug}}" style="padding: 0 10px;">{{ tag.name }}</router-link>
            </span>
          </div>
          <!-- tags end -->
        </div>
        <!-- mtea-bottom end -->

      </article>
      <!-- post article end -->
    </div>

    <SidePanelRelated />
    
  </div>
</template>


<script>
import Prism from 'prismjs'
import { postDateMixin } from '@/mixins'
import { butter } from "@/plugins/buttercms"
import SidePanelRelated from "@/components/SidePanelRelated"

export default {
  name: "SinglePost",
  mixins: [postDateMixin],
  components: {
    SidePanelRelated
  },
  props: ["slug"],
  data() {
      return {
        post: {
            body: '',
            tags: [],
            title: '',
            created: '',
            banner: ''
        }
      }
  },
  methods: {
    getPost() {
      butter.post.retrieve(this.slug)
        .then(res =>{
            this.post.body = res.data.data.body
            this.post.tags = this.tags_with_no_i18n(res.data.data.tags)
            this.post.title = res.data.data.title
            this.post.created = res.data.data.created
            this.post.banner = res.data.data.featured_image
        })
    },
    tags_with_no_i18n(tags) {
      return tags.filter(function(tag){
        if (tag.name != 'en' && tag.name != 'br'){
          return tag;
        }
      })
    }
  },
  created() {
    this.getPost()
  },
  updated() {
    Prism.highlightAll()
  }
};
</script>
